  userId: string
  type: 'discount' | 'cash' | 'gift'  // æŠ˜æ‰£åˆ¸ã€ç°é‡‘åˆ¸ã€ç¤¼å“åˆ¸
  name: string                        // åˆ¸åç§°
  description: string                 // æè¿°
  discountRate?: number              // æŠ˜æ‰£ç‡ï¼šå¦‚ 0.68 è¡¨ç¤º68æŠ˜
  discountPercentage?: number        // æŠ˜æ‰£ç™¾åˆ†æ¯”ï¼šå¦‚ 32 è¡¨ç¤º32%ä¼˜æƒ 
  cashValue?: number                 // ç°é‡‘é¢å€¼ï¼ˆåˆ†ï¼‰
  minAmount?: number                 // æœ€ä½æ¶ˆè´¹é‡‘é¢ï¼ˆåˆ†ï¼‰
